    var getRmb=require('../../common/requset.js')
    export default {
        data(){
            return {
                type:'卖出',
                vinda:'',
                state:'',
                price:'',
                rmb:'',
                clorn:'',
                frte:true,
                checkall:'展示完整信息',
				information:''
            }
        },
        onLoad(option) {
            var that = this
            //获取上个页面携带的数据，并复制
            var vinda = JSON.parse(option.suxang)
            that.vinda = vinda
            var clorn = that.vinda[1]
            that.clorn = that.vinda[1]
            that.state = vinda[0][0].order_status
            if(that.state == 103) {
                that.state = '待确认'
            }
			that.information=that.vinda[0][0]
            that.price = that.vinda[0][0].sale_money
            that.rmb = getRmb.getrmb(that.price)
        },
        methods:{
            btn:function () {
                var that = this
                that.frte=!that.frte;
                if(that.frte==false){
                    that.checkall='收起';
                }else{
                    that.checkall='展示完整信息'
                }
            }
        }
    }
